.card {
  background: $white;
  border-radius: 10px;
  padding: 30px 20px;

  &__section {
    > * {
      margin: 0;
    }
  }

  &__cta {
    margin-top: 20px;
  }

  &__image {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }

  &--horizontal {
    padding: 0;

    @include tablet {
      display: flex;
    }

    .card__cta {
      margin-bottom: 20px;
    }

    .card__image {
      margin-left: 60px;
      margin-top: 0;
    }

    .card__section {
      padding: 30px;

      @include tablet {
        padding-right: 0;
        width: 50%;
      }
    }

    .card__section--image {
      display: flex;
      align-items: flex-end;
      padding: 0;

      img {
        border-bottom-right-radius: 10px;
        max-width: 100%;
        align-self: flex-start;
      }
    }
  }

  &--full-width-image {
    position: relative;
    padding: 0;

    .card__image {
      margin-top: -20px;

      img {
        max-width: 100%;
      }
    }

    .card__section {
      padding: 30px 30px 0;
      position: relative;
    }
  }
}